import React from "react"
import styled from "styled-components"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Image from "../components/image"
import Ingredients from "../components/ingredients"

import { graphql } from "gatsby"

const ImageContainer = styled.div.attrs({ className: "ImageContainer" })`
  margin: 1em;
  width: 60%;
  position: relative;
  // z-index: 3;

  &:before {
    content: " ";
    position: absolute;
    left: -15px;
    top: -10px;
    width: 100%;
    height: 70%;
    background: rgba(168, 22, 22, 0.5);
    z-index: -1;
  }

  &:after {
    content: " ";
    position: absolute;
    left: 20px;
    bottom: -10px;
    width: 100%;
    height: 100%;
    background: rgba(60, 158, 214, 0.5);
    z-index: -2;
  }
`

const SoapTemplate = ({ data }) => {
  console.log("SOAP TEMPLATE, data:", data)

  const ingredients = data.allIngredient.edges.map(({ node }) => {
    return { name: node.name, tooltipId: node.tooltipId }
  })

  return (
    <Layout>
      <SEO title="..." />
      <h1>{data.product.name}</h1>
      <ImageContainer>
        <Image name={data.product.imageId} />
      </ImageContainer>

      <p>{data.product.description}</p>

      <p>{data.product.content}</p>

      <Ingredients ingredients={ingredients} />
    </Layout>
  )
}

// TODO NOTE query data here for the ingredients component, in the right format

export const query = graphql`
  query($productId: String!, $inci: [String!]!) {
    product(productId: { eq: $productId }) {
      name
      description
      content
      imageId
    }
    allIngredient(filter: { ingredientId: { in: $inci } }) {
      edges {
        node {
          id
          ingredientId
          name
          tooltipId
        }
      }
    }
  }
`

export default SoapTemplate
